body {
	font-family: $Futura;
	font-weight: 500;
	font-size: 16px;
	color: $gray;
	line-height: 1.88;
	@include sm {
		font-size: 13px;
		line-height: 1.54; } }

.inner {
	max-width: 1060px;
	padding:  0 30px;
	margin: 0 auto;
	@include sm {
		padding: 0 15px; } }
.in {
	max-width: 1260px;
	padding: 0 30px;
	margin: 0 auto;
	@include sm {
		padding: 0 15px; } }

.title-lg {
	font-size: 50px;
	font-weight: 500;
	color: $white;
	text-transform: uppercase;
	font-family: $Futuras;
	line-height: 1.2;
	@include sm {
		font-size: 25px; } }

.subtitle-md {
	display: inline-block;
	color: #191926;
	font-family: $Futuras;
	font-weight: 500;
	font-size: 30px;
	text-transform: uppercase;
	position: relative;
	line-height: 1.2;
	@include sm {
		font-size: 15px; }
	&:before {
		content: '';
		position: absolute;
		top: 50%;
		right: -100px;
		transform: translateY(-50%);
		height: 2px;
		width: 80px;
		background-color: #d4d4d4;
		@include sm {
			width: 40px;
			right: -60px; } }
	&:after {
		content: '';
		position: absolute;
		top: 50%;
		left: -100px;
		transform: translateY(-50%);
		height: 2px;
		width: 80px;
		background-color: #d4d4d4;
		@include sm {
			width: 40px;
			left: -60px; } } }

.title-sm {
	color: $black;
	font-size: 24px;
	font-weight: 500;
	font-family: $Futuras;
	line-height: 1.5;
	@include sm {
		font-size: 14px; }
	&_white {
		color: $white;
		text-transform: uppercase; } }

.btn {
	display: inline-block;
	background-color: $blue;
	padding: 0 85px;
	height: 50px;
	line-height: 50px;
	font-size: 14px;
	text-decoration: none;
	color: $white;
	border-radius: 5px;
	font-family: $Futuras;
	@include sm {
		padding: 0 40px;
		font-size: 13px;
		height: 35px;
		line-height: 35px; }
	&_green {
		background-color: $green; } }

.top-block {
	background-position: 50% 50%;
	background-size: cover;
	background-repeat: no-repeat;
	position: relative;
	&__title-lg {
		text-align: center;
		padding: 103px 0;
		@include sm {
			padding: 51px 0; } } }

.link {
	text-decoration: none;
	font-size: 20px;
	font-weight: 500;
	color: $blue;
	line-height: 1.2;
	margin-top: 20px;
	display: inline-block;
	@include sm {
		display: none; } }
