.error {
	background-color:  $lightGray;
	padding: 209px 0 117px;
	@include md {
		padding: 50px  0; }
	&__inner {
		display: flex;
		align-items: stretch;
		@include md {
			flex-direction: column; } }
	&__number {
		font-size: 200px;
		color: $white;
		background-color: $black;
		display: flex;
		align-items: center;
		padding: 0 28px;
		box-shadow: 0 29px 40px -12px rgba(0, 0, 0, 0.13);
		@include md {
			display: block;
			text-align: center; }
		@include sm {
			font-size: 100px; } }
	&__info {
		// padding-left: 52px
		padding: 130px 129px 130px 52px;
		background: $white;
		@include md {
			padding: 52px;
			text-align: center; }
		@include sm {
			padding: 20px; } }
	&__title-sm {
		margin-bottom: 15px; }
	&__descr {
		p {
			font-size: 16px;
			font-family: $Futuras;
			line-height: 1.88;
			color: $gray;
			letter-spacing: normal;
			@include sm {
				font-size: 14px; } } } }
