.press {
	background-color:  $lightGray;
	&__inner {
		padding: 60px 0 80px;
		@include sm {
			padding: 15px; } }
	&__row {
		background-color: $white;
		padding-bottom: 50px;
		@include sm {
			padding-bottom: 25px; } }
	&__row:not(:last-child) {
		margin-bottom: 40px;
		@include sm {
			margin-bottom: 15px; } }
	&__info {
		text-align: center;
		max-width: 725px;
		padding: 0 10px;
		margin: 0 auto; }
	&__img {
		padding-bottom: 23%;
		background-position: 50% 50%;
		background-size: cover;
		background-repeat: no-repeat;
		margin-bottom: 20px;
		@include sm {
			margin-bottom: 10px;
			padding-bottom: 50%; } }
	&__title-sm {
		margin-bottom: 13px;
		@include sm {
			margin-bottom: 10px; } }
	&__descr {
		font-size: 16px;
		color: $gray;
		margin-bottom: 40px;
		line-height: 1.88;
		font-family: $Futuras;
		@include sm {
			font-size: 14px;
			margin-bottom: 20px; } }
	&__btn {
		margin-top: 40px;
		@include sm {
			margin-top: 20px; } } }
